import service from "@/uilt/request";

export function createDriverinfo(obj)
{
    return service.post("/api/driverinfo",obj);
}

/**
 * 
 * @param {数据表字段} key 
 * @param {数据标值} val 
 */
export function updateDriverInfo(key,val){
    var arr = {};
    arr[key] = val;
    return service.get("/api/driverinfo/putDriverInfo?key="+key+"&val="+val);
}


